import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import { useAuth } from "../Context/AuthContext";
import Todo from "./Todo";
import {Button} from 'react-bootstrap'

export default function DashBoard() {
  const [todos, setTodos] = useState([]);
  const [inputText, setInputText] = useState('');
  const [filterTodo, setFilterTodo] = useState([])

  // const {currentUser, logout} = useAuth()
  //   const [error, setError] = useState("");
  //   const history = useHistory()
  //   async function handleLogOut() {
  //       setError('')

  //       try {
  //           await logout();
  //           history.push("/login");
  //       } catch  {
  //           setError("Unable to log out")
  //       }

  //   }

  const updateHandler = (e) => {
    if (e.target.value === 'all') {
      setFilterTodo(todos.filter(todo => todo));
    }
    if (e.target.value === 'completed') {
      setFilterTodo(todos.filter(todo => (todo.completed ? todo: '')));
      
    }
    if (e.target.value === 'remaining') {
      setFilterTodo(todos.filter(todo => (todo.completed ? '': todo)));
      
    }
  }
  const inputHandler = (e) => {
    setInputText(e.target.value)
  } 

  const todoAddHandler = (e) => {
    e.preventDefault()
    if (inputText !== '')
    
    {
      const id = Math.random() * 1000;
      setFilterTodo([...filterTodo, {
        completed: false,
        text: inputText,
        id:id,
      }])
      setTodos(
      [...todos, {
        completed: false,
        text: inputText,
        id: id,
      }]
    
      )
    setInputText('');}
    
  }


  return (
    <>
      <div className="container">
        <div className="box">
                  <h1>TodDo List</h1>
          <div className="form">
            <form>
              <input onChange={ inputHandler } value={inputText} type="text" placeholder="Add Task"></input>
              <button onClick={todoAddHandler}>
                <i className="fas fa-plus"></i>
              </button>
            </form>
            <form>
              <select defaultValue="all" onChange={updateHandler}>
                <option value="all" >All</option>
                <option value="completed">Completed</option>
                <option value="remaining">Remaining</option>
              </select>
            </form>
          </div>

          <div className="todolist">
            <ul>

              {
                filterTodo.map(item => ( 
                  <Todo key={item.id} item={item} setTodos = {setTodos} todos = {todos} setFilterTodo={setFilterTodo} filterTodo={filterTodo} />
                ))
              }
              
    
                      </ul>
                      </div>
              <div className="w-100 text-center mt-2">
                {/* <Button variant="Link" onClick={handleLogOut} className="btn btn-primary w-50  align-text-top h-100" >Log Out</Button> */}
            </div>
          </div>
              
      </div>
    </>
  );
}

